.Intro-Container {
    z-index: 12;
    position: absolute;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    flex-direction: column;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);
}


.H1-Container {
    display: flex;
    flex-direction: column;
    color: #FFFFFF;
    gap: clamp(8px, 2vw, 24px);
    max-width: clamp(335px, 40vw, 805px);

}

.H1-Container h2 {
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(16px, 2vw, 24px);
    line-height: clamp(22px, 2vw, 32px);
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);
}

.H1-Container h1 {
    font-family: 'Gilroy-Bold';
    font-weight: 400;
    font-size: clamp(24px, 3vw, 65px);
    line-height: clamp(32px, 3vw, 72px);
    letter-spacing: -1.5px;
    text-align: left;

    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);
}

.H1-Container p {
    max-width: 550px;
    font-family: "Gilroy-Regular";
    font-size: clamp(14px, 2vw, 20px);
    line-height: clamp(18px, 2vw, 32px);
    font-weight: 400;
    letter-spacing: 0em;
    text-align: left;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);
}



.Register-Container {
    margin-top: 18px;

    max-width: 500px;
    height: auto;
    display: flex;
    flex-wrap: wrap;

}

.reg-box {
    width: 361px;
    height: 62px;

    display: flex;
    flex-direction: row;

    background-color: #FFFFFF;

    border-radius: 3px 0px 0px 3px;

    align-items: center;

    justify-content: space-around;

    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#ddd {
    width: 20%;
    display: flex;
    margin-left: 24px;

    border: none;
    outline: none;
    appearance: none;

    background: none;
    font-family: 'Gilroy-Regular';
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #0A070B;
}

#tel-input {
    border: none;
    outline: none;
    width: 80%;
    height: 70%;
    margin-left: 24px;
    padding-left: 24px;
    border-left: 0.5px solid rgba(244, 27, 60, 0.600);

    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #807E81;
}


#tel-input.inputVal::-webkit-input-placeholder {
    color: #F41B3B;
}






.reg-btn {
    cursor: pointer;
    width: 138px;
    height: 62px;

    background: #F41B3B;
    border-radius: 0px 3px 3px 0px;

    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.reg-btn:active {
    background-color: #a7142a;
}


.reg-btn p {
    user-select: none;
    text-align: center;
    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: #FFFFFF;
}



@media (max-width: 720px) {

    .Register-Container {
        gap: 16px;
        width: 100%;
        height: 48px;

        font-size: 14px;
        line-height: 22px;
    }

    .reg-box {
        max-width: 361px;
        height: 48px;

        border-radius: 3px 3px 3px 3px;
    }

    .reg-btn {
        width: 138px;
        height: 48px;

        border-radius: 3px 3px 3px 3px;

        font-size: 16px;
        line-height: 22px;
    }
}