.BackgroundComponent-Container {
    z-index: 11;
    width: 100%;
    height: clamp(440px, 50vw, 720px);

    position: relative;
}

.Bg-filter {
    user-select: none;
    z-index: 11;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(172.23deg, #000000 4.94%, rgba(0, 0, 0, 0.35) 45.74%, rgba(0, 0, 0, 0.9) 92.94%);
    mix-blend-mode: multiply;
}


.Img-container {
    z-index: 10;
    width: 100%;
    height: 100%;
}

.Img-container img {
    position: relative;
    width: 100%;
    height: clamp(440px, 50vw, 720px);
    object-fit: cover;
}