.feature-container {
    z-index: 2;
    width: 100%;
    height: clamp(440px, 50vw, 800px);

    display: flex;
    flex-direction: column;

    position: relative;
    background: #0A070B;
}

.next-btn, .prev-btn{
    position: absolute;
    z-index: 10;
    cursor: pointer;
}

.next-btn path:hover, .prev-btn path:hover{
    fill: #aaaaaa;
}

.next-btn path:active, .prev-btn path:active{
    fill: #525252;
}

.prev-btn{
    left: 0;
}

.next-btn{
    right: 0;
}


.featureSlider {
    z-index: 2;
    width: 100%;
    height: clamp(440px, 50vw, 800px);

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;

    background: #0A070B;
}



.feature-slide {

    position: relative;
    width: 100%;
    height: 100%;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);
}

.slide-item {
    border-radius: 3px;
    position: relative;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
}

.slide-item img {
    user-select: none;
    z-index: -2;
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    border-radius: 3px;
}







.feature-intro {

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);

    z-index: 2;
    height: 100%;
    max-width: 600px;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    margin-bottom: clamp(20px, 10vw, 105px);


    color: #FFFFFF;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);
}

.feature-intro h1, h2, h3 {
    cursor: pointer;
    user-select: none;
    z-index: 2;
}



.feature-intro h1 {
    font-family: 'Gilroy-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: clamp(20px, 5vw, 38px);
    line-height: clamp(32px, 5vw, 48px);

    letter-spacing: -0.015em;
    text-transform: capitalize;

    margin-bottom: 1px;

}

.feature-intro h2 {
    font-family: 'Gilroy-Medium';
    font-style: normal;
    font-weight: 400;
    font-size: clamp(14px, 3vw, 20px);
    line-height: clamp(22px, 3vw, 24px);

    margin-bottom: 8px;
}

.feature-intro h3 {
    font-family: 'Gilroy-SemiBold';
    font-size: 14px;
    line-height: 20px;
    color: #cecece;

    color-interpolation-filters: linearRGB;
    text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.45);

    margin-bottom: 42px;
}




.feature-btn {
    display: flex;
    flex-direction: row;
    text-align: center;

    gap: 20px;
}

.align-btn {
    gap: 16px;
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    flex: none;
    order: 1;
    flex-grow: 0;

    text-align: center;

    justify-content: center;
}

.align-btn p {
    user-select: none;

    width: 68px;
    height: 52px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding-bottom: 5px;
}

.align-btn svg {
    user-select: none;
}




.playnow-btn, .watchlist-btn {
    cursor: pointer;

    width: 191px;
    height: 66px;
    border-radius: 3px;

    padding: 0px 32px;
    gap: 16px;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;


}


.playnow-btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(40px);
}

.playnow-btn:hover {
    background: rgba(24, 24, 24, 0.733);
}

.playnow-btn:active {
    background: rgba(255, 255, 255, 0.37);

    transition: all 0s;
}




.watchlist-btn {
    display: flex;
    flex-direction: row;
    align-items: center;

    justify-content: center;

    border: 1px solid #FFFFFF;

    background-color: rgba(128, 126, 129, 0.01);
}

.watchlist-btn:hover {
    background: rgba(24, 24, 24, 0.733);
}


.watchlist-btn:active {
    background-color: #807e813d;

    transition: all 0s;
}







@media(max-width: 728px) {
    .feature-slide {

        padding-left: 0px;
        padding-right: 0px;
    }

    .feature-intro h3 {
        display: none;
    }

    .feature-intro {
        width: 100%;
        height: 100%;
        max-width: 100%;
    }

    .feature-btn {
        padding: 0;
        flex-direction: column;
        padding-top: 13px;

        width: 100%;
    }



    .playnow-btn, .watchlist-btn {
        cursor: pointer;

        width: 100%;
        height: 66px;
        border-radius: 3px;

        padding: 0px 32px;
    }

    .prev-btn, .next-btn{
        display: none;
    }
}






















.filter {
    user-select: none;
    z-index: -1;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin: 0;
    top: 0;
    left: 0;
    background: linear-gradient(259.96deg, rgba(0, 0, 0, 0.25) 1.2%, rgba(0, 0, 0, 0.25) 47.01%, rgba(0, 0, 0, 0.25) 100%);
    mix-blend-mode: multiply;
    border-radius: 3px;
}