.Menu-Slider {
    z-index: 20;
    position: absolute;

    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    overflow: hidden;
}


.Menu-Inner::-webkit-scrollbar {
    display: none;
}

.Menu-Inner {
    overflow-y: scroll;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 24px;
    transition: 0s ease-in;
    padding: 20px;
}

.Menu-Inner li {
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;
    list-style: none;
    gap: 8px;
}

.Menu-Inner li a {
    text-decoration: none;
    white-space: nowrap;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;
}

.Menu-Inner li svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Menu-Inner li {
    outline: none;
    cursor: pointer;
    user-select: none;
}

.Menu-Inner li svg path {
    fill: #737174
}

.Menu-Inner li:active svg path {
    fill: #E8ECEF;
}

.Menu-Inner li:active a {
    color: #E8ECEF;

}


@media screen and (min-width: 1280px) {
    .Menu-Slider {
        display: none;
    }
}