/* default style open */

.nav-container ul {

    order: 2;
}

.nav-container ul li {
    user-select: none;
}

.nav-container ul li svg path {
    fill: #737174
}


.nav-container ul li:hover svg path {
    fill: #E8ECEF
}

.nav-container ul li:hover a {
    color: #E8ECEF
}


.nav-container ul li:active svg path {
    fill: #4F4E50
}

.nav-container ul li:active a {
    color: #4F4E50
}



.nav-container {
    z-index: 25;
    width: 100%;

    height: clamp(60px, 10vw, 100px);

    background-color: #0A070B;


    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 24px;

    padding-left: clamp(20px, 5vw, 70px);
    padding-right: clamp(20px, 5vw, 70px);
}




.nav-container ul li a {
    text-decoration: none;
    white-space: nowrap;

    font-family: 'Gilroy-SemiBold';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #807E81;
}

.nav-container ul li {
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;
    list-style: none;

    gap: 8px;
}

.nav-container ul li svg {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-container ul {
    z-index: 10;

    display: flex;

    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
}







.signin-container {

    order: 3;
    gap: clamp(16px, 2vw, 56px);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: auto;

    align-items: center;

    position: relative;
}




.signin-container span {
    display: flex;
    align-items: center;
    justify-content: center;
}

.signin-container span svg {
    display: flex;
    align-items: center;
    justify-content: center;
}


.pesquisa-container {
    display: none;
}

.logo-container {
    order: 1;
    flex-basis: 50%;
    min-width: 106px;
    min-height: 28px;

    display: flex;
    align-items: center;


}

/* default style  close */





/*      MobileFirst 
media query MobileFirst Open*/

@media screen and (max-width: 480px) {
    .nav-container {
        display: flex;
        flex-direction: row;


    }


    .nav-container ul {
        z-index: 10;

        display: none;

        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        gap: 20px;
    }

    .nav-container ul li {
        list-style: none;
    }

    .nav-container ul li a {

        color: #807E81;
    }


}

/*
media query MobileFirst Close*/



/*      iPads/Tablets
media query MobileFirst Open*/

@media screen and (min-width: 481px) {}

/*      iPads/Tablets
media query MobileFirst Close*/

/*      Destok/ Large Screens
media query MobileFirst Open*/

@media screen and (min-width: 1025px) {



    .nav-container ul {
        padding: 20px;

        z-index: 10;


        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .nav-container ul li a {
        text-align: center;

        cursor: pointer;

        color: #807E81;

    }

    .nav-container ul li {
        cursor: pointer;
        height: 22px;


        display: flex;
        align-items: center;
        align-items: center;
        justify-content: center;
        list-style: none;

        gap: 8px;
    }

    .nav-container ul li svg {
        align-self: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }



}

@media screen and (max-width: 1279px) {
    .nav-container ul {
        padding: 20px;

        z-index: 10;
        display: none;

        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .logo-container {
        order: 1;
        flex-basis: 20%;
        min-width: 106px;
        min-height: 28px;

        height: 0;
        display: flex;
        align-items: center;


    }
}


@media screen and (min-width: 1280px) {

    .nav-container ul {
        padding: 20px;

        z-index: 10;
        display: flex;

        flex-direction: row;
        align-items: center;
        gap: 24px;
    }

    .logo-container {
        order: 1;

        max-width: 169px;
        max-height: 41px;

        height: 0;
        display: flex;
        align-items: center;


    }
}














.Profile-Content {
    width: auto;
    height: 46px;

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 12px;

    cursor: pointer;

    position: relative;
}


.Profile-Avatar {
    width: clamp(28px, 5vw, 46px);
    height: clamp(28px, 5vw, 46px);
    border-radius: 50%;
    overflow: hidden;
}

.Profile-Avatar img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}


@keyframes wipe-in-down {
    from {
        clip-path: inset(0 0 100% 0);
    }

    to {
        clip-path: inset(0 0 0 0);
    }
}

[transition-style="in:wipe:down"] {
    animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-down both;
}




#Card-Profile.ActiveProfile {
    display: flex;
}

#Card-Profile {
    top: clamp(60px, 10vw, 100px);
    right: clamp(20px, 5vw, 70px);
    position: absolute;

    width: 257px;
    height: 376px;

    background: #15161C;
    box-shadow: 0px 4px 45px rgba(2, 6, 13, 0.42);
    border-radius: 8px;

    display: none;
    flex-direction: column;
    align-items: center;


    animation: 0.5s cubic-bezier(.25, 1, .30, 1) wipe-in-down both;
}

.Card-avatar-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 14px;

    width: 100%;
    height: 150px;

    letter-spacing: -0.01em;
}

.Card-avatar-container h5 {
    font-family: 'Gilroy-Regular';
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: -0.01em;
    text-align: left;

    color: #FFFFFF;

    opacity: 0.65;
}

.Card-Profile-Flex {
    cursor: pointer;

    height: 56px;
    width: 80%;

    border-top: 1px solid #ffffff0e;

    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 16px;

    letter-spacing: -0.01em;
}

.Card-Profile-Flex p {
    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}

.Card-Profile-Flex h2 {
    font-family: 'Gilroy-Medium';
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #F84550;
}






.Card-avatar {
    width: 62px;
    height: 62px;
    border-radius: 50%;
    overflow: hidden;
}

#Card-Profile img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}